.skill {
	width: 50%;
	float: left;
	display: flex;
	flex-direction: row;
	align-items: center;
}
	
.skill:nth-of-type(2n) .skillImage{
	order: -1;

}
.skill:nth-of-type(2n+1) {
	text-align: right;
	justify-content: flex-end;
}


div.svgImage{
	width: 80px;
    background: $accent;
    margin: 30px;
    padding: 10px;
}

.interests {
	padding-top: 40px;
}
