nav.mainNav {
	height: 60px;
	margin-bottom: -60px;
	z-index: 10;
	position: relative;
	.logo {
		width: 50px;
		padding: 14px 0 10px 20px;

	}
	background: $dark;
	font-family: $source-code;
	.container, ul#menu-main-menu, ul#menu-social-menu {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: center;
		ul {
			list-style: none;
		}
	}
	a {
		color: $light;
		text-transform: uppercase;
	}
	ul#menu-main-menu, ul#menu-social-menu {
		li {
			padding: 0px 10px;
		}
	}
}

nav.mainNavScrolled {
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 10;
}
main {
	position: relative;
	z-index: 2;
}